.form-table {
  table-layout: fixed; }
  .form-table tr:first-child td p {
    margin-top: 10px; }

.image-size__actions .button:before {
  display: inline-block;
  font: 400 1.25em/1 dashicons;
  margin: 5px .28em 0 0;
  float: left; }

.image-size__actions .button.done:before {
  content: "\f147";
  color: #79ba49; }

.image-size__actions .button.pending {
  pointer-events: none; }
  .image-size__actions .button.pending:before {
    content: "\f463";
    color: #555;
    animation: rotation 2s infinite linear; }

.image-size__actions.progress-bar,
.image-size__actions .progress {
  overflow: hidden; }
  .image-size__actions.progress-bar:not(:empty),
  .image-size__actions .progress:not(:empty) {
    padding-right: 10px;
    padding-left: 10px; }

.image-size__actions.progress-bar {
  background-color: rgba(0, 0, 0, 0.15);
  position: relative;
  white-space: nowrap;
  line-height: 27px; }
  @media (min-width: 783px) {
    .image-size__actions.progress-bar {
      font-size: 11px;
      line-height: 24px; } }
  .image-size__actions.progress-bar .progress {
    color: #fff;
    position: absolute;
    top: 0;
    width: 0;
    bottom: 0;
    left: 0;
    background-color: #79ba49;
    transition: width .2s;
    box-sizing: border-box; }
    @media (max-width: 782px) {
      .image-size__actions.progress-bar .progress {
        line-height: 31px; } }
  .image-size__actions.progress-bar .button {
    visibility: hidden;
    width: 1px;
    font-size: 11px;
    line-height: 22px;
    height: 24px; }

.image-size__actions:not(.progress-bar) .progress,
.image-size__actions:not(.progress-bar) .status {
  display: none; }
